import tabbar from "../../utils/tabbar.js"
const tabBar = {
	state:{
		role:'',
		tabBarList:[]
	},
	mutations:{
		setRole(state,role){
			let r = ";"
			state.role = role;
			switch(state.role){
				case '': {
					r = 'admin'
					break
				}
				case '':{
					r = 'lawyer'
					break
				} 
				case '':{
					r = 'enterprise'
					break
				}
				case '':{
					r = 'lawyer'
				}
			}
			state.tabBarList = tabbar[r]
		}
	}
}
export default tabBar